<template>
  <div class="order-wx">
    <ul class="wx-left" >
      <li v-for="(item, index) in data.useProductIconUrls" :key="index"><img v-lazy="item" alt=""></li>
      <!--
      <img src="../../common/imgs/default.png" alt=""> -->
    </ul>
    <div class="wx-con" :class="{'img': data.useProductIconUrls.length > 2}">
    </div>
    <div class="wx-right">
      <p>{{data.faultDetails}}</p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'orderWx',
  props: {
    data: {
      type: Object,
      required: true
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .order-wx
    display: flex
    align-items: center
    height: 147px
    padding: 0px 30px
    .wx-left
      display: flex
      overflow: hidden
      img
        float: left
        box-sizing: border-box
        display: inline-block
        width: 120px
        height: 120px
        border: 1px solid #d2d2d2
        margin-right: 10px
    .wx-con
      flex: 1
      height: 120px
      background-size: 27px 6px
      background-repeat: no-repeat
      background-position: left center
      margin-right: 30px
      .img
        bg-image('../../common/imgs/ellipsis')
    .wx-right
      width: 360px
      display: flex
      align-items: center
      height: 90px
      padding-right: 36px
      bg-image('../../common/imgs/mind/leftright')
      background-size: 15px 24px
      background-repeat: no-repeat
      background-position: right center
      p
        font-size: 22px
        color: #9f9f9f
        height: 90px
        line-height: 30px
        word-break: break-all
        overflow : hidden
        text-overflow: ellipsis
        display: -webkit-box
        -webkit-line-clamp: 3
        -webkit-box-orient: vertical
</style>



